input[type=checkbox] {
  display: none;
  margin: .5em;
  padding: 0.2em; }
  input[type=checkbox] + label {
    display: block;
    margin: 0.2em;
    cursor: pointer;
    padding: 0.2em; }
  input[type=checkbox] + label:before {
    content: "\2714";
    border-radius: 4px;
    display: inline-block;
    border: 0.1em solid #3058913d;
    width: 1em;
    height: 1em;
    font-size: 12px;
    padding-left: 0.2em;
    padding-bottom: 0.3em;
    margin-right: 10px;
    vertical-align: bottom;
    color: transparent;
    -webkit-transition: .2s;
    transition: .2s; }
  input[type=checkbox] + label:active:before {
    -webkit-transform: scale(0.5);
            transform: scale(0.5); }
  input[type=checkbox]:checked + label:before {
    background-color: #5ea6ff;
    border-color: #799cc8;
    color: #ffffff; }
  input[type=checkbox]:disabled + label:before {
    -webkit-transform: scale(1);
            transform: scale(1);
    border-color: #aaa; }
  input[type=checkbox]:checked:disabled + label:before {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: #8da7fc;
    border-color: #bfb; }

input[type=number] {
  width: 2em;
  height: 41px;
  font-size: 30px;
  padding-left: 15px;
  text-align: center;
  font-family: serif;
  color: #25256deb;
  border: 1px solid #7291a6; }

.radio-button {
  margin: 0.2em 0;
  padding: 0.2em 0; }
  .radio-button input[type="radio"] {
    position: absolute;
    opacity: 0; }
    .radio-button input[type="radio"] + .radio-label {
      margin: 0.2em;
      cursor: pointer;
      padding: 0.2em;
      margin-right: 10px; }
      .radio-button input[type="radio"] + .radio-label:before {
        content: '';
        background: #d3e4d1;
        border-radius: 100%;
        border: 1px solid #2d657721;
        display: inline-block;
        width: 0.9em;
        height: 0.9em;
        position: relative;
        top: 0;
        margin-right: .5em;
        vertical-align: top;
        cursor: pointer;
        text-align: center;
        -webkit-transition: all .3s ease;
        transition: all .3s ease; }
    .radio-button input[type="radio"]:checked + .radio-label:before {
      background-color: #64a9ff;
      box-shadow: inset 0 0 0 4px #fbfeff; }
    .radio-button input[type="radio"]:focus + .radio-label:before {
      outline: none;
      border-color: #2083FE; }
    .radio-button input[type="radio"]:hover {
      border: 1px solid #929292; }
    .radio-button input[type="radio"]:disabled + .radio-label:before {
      opacity: 0.4; }
    .radio-button input[type="radio"] + .radio-label:empty:before {
      margin-right: 0; }

